#container{
  background-color: #F7E7CE;
  width: 100vw;
}
.pages{
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items:center;
}
#linha{
  margin:0;
  height: 1vh;
  background-color: #171818;
  border: none;
}
h1{
    font-size: 15rem;
    margin: -10px 0;
    color: #f5f4f0;
}
#intro{
  z-index: 3;
  display: flex;
  flex-direction:column;
}
#initial{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: white;
  width: 100%;
  background-repeat: no-repeat;
  position:relative;
}
#boulder{
  height: 100vh;
  background-image: url("/img/boulder.jpg");
  width: 100vw;
  z-index: 1;
  background-repeat: no-repeat;
}
.overlay{
  background-color:rgba(0, 0, 0, 0.445);
  width: 100vw;
  height: 100%;
  z-index: 2;
}
#intro,
#overlay,
#boulder{
  position:absolute;
}
#about{
  margin-top: 5%;
  margin-bottom: -5%;
}
h2{
  font-size: 300%;
}
h3{
  font-size: 200%;
}
#h3{
  display: flex;
  justify-content: flex-end;
  width: 100vh;
  margin-left: 25%;
}
#imagetext{
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
#image{
  width: 60%;
  margin:5.1% 0 -11% -10%;
}
#text{
  font-size: 22px;
  width: 40%;
}
#projects{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.buttons{
  width: 20%;
  height: 20%;
  background-color: tomato;
  border-width: 10px;
  border-color: tomato;
}

#getintouche{
  font-size: 17vh;
}
#contatos{
  display: flex;
  flex-flow: column wrap;
  height: 70%;
  justify-content: center;
  width: 70%;
  margin:30px
}
.contatos{
  font-size: 6vh;  
}